body {
  background-image: url("bg.b7f3f9b4.jpg");
}

.container {
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

@media screen and (width <= 768px) {
  .container {
    width: 610px;
  }
}

@media screen and (width <= 480px) {
  .container {
    width: 100%;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body, h1, h2, h3, h4, h5, p, ul, figure {
  margin: 0;
  padding: 0;
}

ul, menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  font-family: Roboto;
}

#postsContainer {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px 160px;
  display: flex;
}

@media screen and (width <= 768px) {
  #postsContainer {
    gap: 20px 25px;
  }
}

@media screen and (width <= 480px) {
  #postsContainer {
    gap: 20px 10px;
  }

  #postsContainer .event-image {
    width: 130px;
    height: 160px;
  }

  #postsContainer .event-card {
    width: 130px;
  }

  #postsContainer .event-info h2 {
    font-size: 14px;
  }

  #postsContainer .event-info p {
    font-size: 12px;
  }

  #postsContainer .post-line {
    width: 100px;
    height: 100px;
  }
}

.event-card {
  color: #fff;
  text-align: center;
  background: none;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  width: 180px;
  font-family: Arial, sans-serif;
  position: relative;
}

.event-image {
  background-position: center;
  background-size: cover;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  height: 227px;
  position: relative;
}

.event-date {
  background: #000000b3;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  font-weight: 400;
  position: absolute;
  top: 10px;
  right: 10px;
}

.event-info {
  padding: 15px;
}

.event-info h2 {
  color: #f0f;
  margin: 10px 0;
  font-size: 18px;
}

.event-info p {
  margin: 5px 0;
  font-size: 14px;
  font-weight: 600;
}

.posts use {
  fill: #fff;
  margin-right: 5px;
  display: inline;
  width: 20px !important;
  height: 20px !important;
}

.posts svg {
  fill: #fff;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  display: inline;
}

.post-line {
  z-index: 100;
  background: none;
  border: 1px solid #dc56c54d;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  width: 150px;
  height: 150px;
  position: absolute;
  top: -10px;
  right: -10px;
}
